}

.imageAndText {
    position: relative;
    display: flex;
    align-items: flex-end;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
   max-width: 527px;
   white-space: wrap;
   border-radius: 15px;
   background-color: #0066CC;
   padding: 7px 15px;
   color: white;
   margin-right: 12px;
}

.text:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 52px;
    width: 0;
    height: 0;
    border-top: 21px solid transparent;
    border-bottom: 0 solid transparent;
    border-left: 20px solid #0066CC;
}

